.collapse-bar {
    width: 100%;
    margin: auto;
    height: 54px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 5px;
    background-color: #ff6060;
    padding: 10px 20px 10px 15px;
    border: none;
    cursor: pointer;

    &__heading {
        color: #ffffff;
        font-family: Montserrat;
        font-size: 24px;
        font-weight: 700;
    }

    &__icon {
        transition: transform 0.2s ease-out;
    }
}

.active-bar > .collapse-bar__icon {
    transform: rotate(180deg);
}

.collapse-content {
    margin: auto;
    width: 100%;
    max-height: 0px;
    transition: max-height 0.2s ease-out;
    overflow: hidden;
    background-color: #f6f6f673;

    &__text {
        padding: 20px;
        color: #000;
        font-family: Montserrat;
        font-size: 18px;
        font-weight: 400;
        white-space: pre-line;
    }
}

@media (max-width: 767px) {
    .collapse-bar {
        height: 30px;
        padding-right: 10px;
        &__heading {
            font-size: 13px;
        }
    }

    .collapse-content {
        &__text {
            padding: 10px;
            font-size: 12px;
        }
    }
}
